.TodoItem {
  display: -ms-flexbox;
  display: flex;
  height: 3em;
  -ms-flex-align: center;
      align-items: center;
}
.TodoItem input[type="checkbox"] {
  width:1.2em;
  height:1.2em;
  margin-left: .5em;
  vertical-align: middle;
}

.TodoItem input:checked~label {
  text-decoration: line-through;
  opacity: .5;
}
.TodoItem .title {
  font-size: 1.1em;
  padding: .3em;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.TodoItem> .del-btn{
  display: block;
  border: none;
  color: #b9b9b9;
  outline:none;
  font-size: 1.5em;
  cursor: pointer;
}

.TodoItem> .del-btn i {
  margin-right: .3em;
  font-style: normal;
  vertical-align:text-bottom;
}

.TodoItem> .del-btn i:hover {
  color:#ff4f4a;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}




